.page-header {
  background-color: #1B2D37;
  background-image: url("/img/background-desktop@1x.png");
  color: #FFFFFF; }

.page-header__wrapper {
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 13%;
  padding-right: 13%;
  display: flex;
  justify-content: space-between; }

.page-header__container {
  margin-top: 33px;
  width: 50%; }

.page-header__logo-name {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  margin: 0; }

.page-header__logo-description {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
  margin: 0; }

.user-nav {
  width: 50%;
  margin-top: 33px; }

.user-nav ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0; }

.user-nav li {
  padding-bottom: 20px; }

.user-nav__mail {
  position: relative;
  padding-left: 4%;
  padding-right: 6%; }

.user-nav__mail::before {
  position: absolute;
  content: "";
  display: block;
  width: 19px;
  height: 14px;
  background-image: url("/img/envelope.svg");
  top: 3;
  left: -11%; }

.user-nav__phone {
  position: relative;
  padding-right: 2%;
  white-space: nowrap; }

.user-nav__phone::before {
  position: absolute;
  content: "";
  display: block;
  width: 17px;
  height: 17px;
  background-image: url("/img/phone-call.svg");
  top: 3;
  left: -18%; }

.user-nav a {
  color: #ffffff; }
  .user-nav a:hover {
    color: rgba(255, 255, 255, 0.5); }
  .user-nav a:active, .user-nav a:focus {
    color: rgba(255, 255, 255, 0.3); }

.user-nav .user-nav__button {
  font-weight: bold;
  background-color: transparent;
  border: 1px solid #FFFFFF;
  padding-top: 14px;
  padding-bottom: 18px;
  padding-left: 12px;
  padding-right: 13px; }
  .user-nav .user-nav__button:hover {
    background-color: transparent;
    color: rgba(255, 255, 255, 0.5);
    border-color: rgba(255, 255, 255, 0.5); }
  .user-nav .user-nav__button:active, .user-nav .user-nav__button:focus {
    background-color: transparent;
    color: rgba(255, 255, 255, 0.3);
    border-color: rgba(255, 255, 255, 0.3);
    outline: none; }

@media (max-width: 1023px) {
  .user-nav__mail a,
  .user-nav__phone a,
  .user-nav__call {
    display: none; }
  .user-nav ul {
    justify-content: flex-end; } }

body {
  font-family: "PT Mono", "Arial", sans-serif;
